
.nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  height: 49px;
}
.nav-img {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 0 18px;
}
/*  */
.nav-center {
  margin: 0 auto;
}
.nav .el-tabs {
  padding-top: 5px;
}
.nav .el-tabs__item {
  padding: 0 24px;
  font-size: 13px;
  color: #1a1a1a;
}
.nav .el-tabs__item.is-active {
  color: #4bbf9e;
}
.nav .el-tabs__nav-wrap::after {
  display: none;
}
.nav .el-tabs__active-bar {
  display: none;
}
.nav-right {
  position: relative;
  right: 20px;
  height: 49px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  margin-left: 24px;
}
.nav-search,
.nav-drop,
.nav-lang {
  padding: 12px 11px 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  border-right: 1px solid #e5e5e5;
}
.nav-search img,
.nav-drop img,
.nav-lang img {
  width: 26px;
  height: 26px;
}
.nav-search img:hover,
.nav-drop img:hover,
.nav-lang img:hover {
  cursor: pointer;
}
.el-popper[x-placement^="bottom"] {
  margin-top: 5px;
}
.el-dropdown-menu {
  padding: 0;
}
.el-dropdown-menu__item {
  line-height: 38px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 0 45px 0 0;
  font-size: 13px;
  color: #333333;
}
.el-dropdown-menu__item--divided {
  margin: 0;
}
.el-dropdown-menu__item--divided:before {
  display: none;
}
.el-dropdown-menu__item img {
  padding: 0 10px 0 15px;
}
/*  */
.el-popover {
  height: 398px;
  top: 88px !important;
  left: 40% !important;
  margin-left: 0px !important;
  padding: 0;
}
.navbar-item {
  margin-bottom: 0;
  padding-bottom: 11px;
}
.el-form h2 {
  margin: 0;
  display: block;
  font-size: 20px;
  color: #59c2ad;
  text-align: center;
  padding-top: 24px;
  padding-bottom: 17px;
}
.close {
  position: absolute;
  top: 22px;
  right: 27px;
}
.navbar-input .el-input__inner {
  background: white;
}
.navbar-item .el-form-item__label {
  font-size: 13px;
  color: #4d4d4d;
}
.navbar-item .el-form-item__content {
  width: 260px;
  height: 40px;
  display: block;
  margin: 0 auto;
}
.el-form--label-top .el-form-item__label {
  padding: 0;
}
.el-form-item__label {
  padding-left: 28px !important;
}
.el-button {
  width: 100px;
  height: 40px;
  display: block;
  background: #59c2ad;
  margin: 0 auto;
  color: #ffffff;
  font-size: 14px;
  border-radius: 4px;
}




