
.fade-enter-active {
  -webkit-transition: all 0.8s ease;
  transition: all 0.8s ease;
}
.fade-leave-active {
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
.list-item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  padding: 8px 17px 8px 18px;
}
.label {
  width: 197px;
  cursor: pointer;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: #4d4d4d;
  font-size: 12px;
  line-height: 16px;
}
.label:hover {
  color: #1a1a1a;
}
.labelactive {
  padding-top: 6px;
  padding-bottom: 6px;
  background-color: #f2f2f2;
}
.hot {
  font-size: 12px;
  color: #dc3131;
}
.list-item img {
  cursor: pointer;
  width: 16px;
  height: 16px;
}



