
.login {
  background: #5bba9d;
  height: 737px;
}
.login .login-page {
  width: 340px;
  display: block;
  background: #ffffff;
  border: #5bba9d 1px;
  border-radius: 6px;
  -webkit-box-shadow: -2px 0px 14px rgba(73, 73, 73, 0.1);
          box-shadow: -2px 0px 14px rgba(73, 73, 73, 0.1);
  margin: 77px auto 0px auto;
}
.login .login-page .head-pic {
  width: 340px;
  height: 39px;
  padding-top: 29px;
  margin: 0 auto;
}
.login .login-page .head-pic img {
  width: 161px;
  height: 39px;
  margin: 0 auto;
  display: block;
}
.login .login-page .login-list {
  padding: 37px 40px 0px 40px;
}
.login .login-page .login-list .itema {
  padding-top: 20px;
}
.login .login-page .login-list .item span {
  color: #4d4d4d;
  font-size: 13px;
}
.login .login-page .login-list .item .el-input__inner {
  border: none;
  border-radius: 0px;
  border-bottom: 1px solid #e5e5e5;
  font-size: 13px;
  color: #1a1a1a;
  padding: 12px 0px;
  background: #ffffff;
  line-height: 13px;
}
.login
  .login-page
  .login-list
  .item
  .el-input__inner::-webkit-input-placeholder {
  font-size: 13px;
  color: #b3b3b3;
}
.login .login-page .login-list .item .el-input__inner:-moz-placeholder {
  color: #b3b3b3 !important;
  font-size: 13px;
  /* Mozilla Firefox 4 to 18  */
}
.login .login-page .login-list .item .el-input__inner::-moz-placeholder {
  color: #b3b3b3 !important;
  font-size: 13px;
  /* Mozilla Firefox 19+  */
}
.login .login-page .login-list .item .el-input__inner:-ms-input-placeholder {
  color: #b3b3b3 !important;
  font-size: 13px;
  /* Internet Explorer 10+  ie*/
}
.login .login-page .login-list .item .row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.login .login-page .login-list .item .row .left {
  padding-right: 14px;
}
.login .login-page .login-list .item .row .right {
  width: 80px;
  height: 38px;
  background: #e5e5e5;
}
.login .login-page .login-button {
  padding-top: 25px;
}
.login .login-page .login-button .el-button {
  width: 260px;
  height: 38px;
  margin: 0 auto;
  display: block;
  border-radius: 4px;
  background: #5bba9d;
}
.login .login-page .login-button .el-button span {
  font-size: 15px;
  color: #ffffff;
}
.login .login-page .register {
  width: 100%;
  text-align: center;
  padding: 23px 0px 31px 0px;
}
.login .login-page .register span {
  font-size: 12px;
  color: #b3b3b3;
}
.login .login-page .register span a {
  font-size: 12px;
  color: #6982ff;
}
.active {
  border-bottom: 1px solid #5bba9d;
}
.login-label,.login-label2,.login-label3 {
  position: relative;
}
.login-p,.login-p2,.login-p3 {
  position: absolute;
  left: 0;
  top: 2px;
  margin: 0;
  padding: 0;
  font-size: 13px;
  color: #b3b3b3;
}



