
.pic-main {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  background-color: #f5f9f8;
}
.open-fenku {
  width: 12px;
  height: 91px;
  background-color: #ffffff;
  -webkit-box-shadow: 0px 2px 4px rgba(73, 73, 73, 0.1);
          box-shadow: 0px 2px 4px rgba(73, 73, 73, 0.1);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.open-fenku img {
  display: inline-block;
  cursor: pointer;
  /* padding: 2px; */
  margin: auto 0;
  /* height: 16px; */
}
.fenku {
  background-color: #ffffff;
  width: 215px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 700px;
}
/*  */
.el-icon-caret-right:before {
  content: "";
}
.el-icon-caret-right::after {
  content: "\e791";
}
/*  */
.container-water-fall {
  margin-left: 26px;
  padding-top: 24px;
  padding-left: 4px;
  width: 100%;
}
.cell-item {
  padding: 0;
  margin-bottom: 24px;
  /* widows: 296px; */
}
.mask,
.mask2 {
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
  opacity: 1;
  background: #000000;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: auto;
  padding-bottom: -1px;
  margin-bottom: -4px;
  position: relative;
}
.cell-item:hover {
  opacity: 0.7;
}
.descripe {
  position: absolute;
  bottom: 22px;
  left: 13px;
  width: 227px;
  color: #ffffff;
  font-size: 15px;
  margin-right: 56px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  /* display: none; */
}
/*  */
.fenku-all {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  /* width: 1200px; */
}
.fenku-img {
  width: 296px;
  margin: 0 26px 26px 0;
  position: relative;
}
.fenku-img img {
  width: 296px;
  height: 280px;
}
.fenku-img img:hover {
  opacity: 0.7;
}
.fenku-descripe {
  font-size: 20px;
  color: #ffffff;
  position: absolute;
  top: 129px;
  /* width: 280px; */
  left: 60px;
  text-align: center;
}
.fenku-descripe2 {
  font-size: 14px;
  color: #e6e6e6;
}
.fenku-descripe3 {
  position: absolute;
  top: 176px;
  left: 160px;
  font-size: 14px;
  /* color: #e6e6e6; */
  padding: 6px 10px;
  width: 340px;
  border: #e6e6e6 1px solid;
  /* height: 100px; */
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background-color: #ffffff;
  z-index: 10;
}
.fenku-descripe3 span {
  font-size: 12px;
  /* color: #e6e6e6; */
}
.mask2 {
  height: 280px;
}
.open-fenku img {
  width: 12px;
  height: 12px;
}
/*  */
.pic-searchbar {
  padding: 13px 0px 37px 0px;
}
.search {
  margin: 0 auto;
  /* padding-top: 13px; */
  /* padding: 13px 0px 37px 0px; */
  width: 872px;
  height: 50px;
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  font-size: 15px;
  color: #b3b3b3;
}
.search input {
  width: 872px;
  height: 26px;
  padding: 12px 0 12px 54px;
  color: #b3b3b3;
  border-radius: 4px;
  border: #cccccc solid 1px;
  /* background-color: rgba(0, 0, 0, 0.5) ; */

  /* opacity: 0.5; */
}
/*  */
.search input::-webkit-input-placeholder,
.filter input::-webkit-input-placeholder {
  font-size: 15px;
  color: #b3b3b3;
}
.search input:-ms-input-placeholder,
.filter input::-ms-input-placeholder {
  font-size: 15px;
  color: #b3b3b3;
}
.search input::-moz-placeholder,
.filter input::-moz-input-placeholder {
  font-size: 15px;
  color: #b3b3b3;
}
.el-input input::-webkit-input-placeholder {
  /* font-size: 15px; */
  color: #ffffff;
}
.el-input input::-ms-input-placeholder {
  /* font-size: 15px; */
  color: #ffffff;
}
.el-input input::-moz-input-placeholder {
  /* font-size: 15px; */
  color: #ffffff;
}
.sear-img {
  position: absolute;
  top: 14px;
  left: 16px;
}
.search .picturelib-span {
  position: absolute;
  top: 14px;
  right: 74px;
  color: #1a1a1a;
  font-size: 15px;
  line-height: 26px;
}
.search-setting {
  position: absolute;
  top: 0;
  right: 14px;
  width: 26px;
  height: 50px;
  padding-left: 14px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  border-left: 1px solid #b2b2b2;
}
.search .search-span:hover,
.search-setting img:hover {
  cursor: pointer;
}
.gallery {
  margin-left: 28px !important;
}
.gallery .el-footer {
  position: absolute;
  left: 0;
}



