
























































































/* search */
.header{
  position: relative;
}
.all{
  position: absolute;
  z-index: 99;
  left: 0;
  right: 0;
}
.el-header{
  height:auto !important;
  padding:0px;
}
/*  */
.carousel{
  width: 100%;
  height: inherit;
  min-height: 540px;
  min-width: 1400px;
  /* background-size: 100% 100%;
  background-repeat: no-repeat; */
}
.el-carousel__indicators--horizontal{
  bottom: 42px;
  padding-left: 20px;
}
.el-carousel__button{
  width: 14px;
  height: 14px;
  border-radius: 50%;
  opacity: 0.5;
}
.el-carousel__arrow{
  width: 52px;
  height: 80px;
  border-radius: 0;
}
.slideshow-block .el-icon-arrow-left{
  background-image: url(../assets/images/2-1/ico-14-lastbanner.png) ;
  width: 52px;
  height: 80px;
}
.slideshow-block .el-icon-arrow-left::before{
 content: " ";
}
.slideshow-block .el-icon-arrow-right::before{
 content: " ";
}
.slideshow-block .el-icon-arrow-right{
  background-image: url(../assets/images/2-1/ico-13-nextbanner.png) ;
  width: 52px;
  height: 80px;
}
.text{
  position: absolute;
  bottom: 0;
  left: 0;
}
.text a{
  font-size: 18px;
  color: #ffffff;
  display: block;
  padding-left: 46px;
  padding-bottom: 46px
}
/*  */
.header .content{
    padding-top: 75px;
}
.header .content .content-text{
  width:584px;
  height: 90px;
  display: block;
  background: #4bcc94;
  margin: 0 auto;
}
.header .content .content-text span{
  text-align: center;
  font-size: 38px;
  color: #ffffff;
  /* line-height: 38px;
  display: inline-block;
  width: 100%; */
  /* vertical-align:middle;    */
  line-height: 90px;
  padding: 0px 45px;
}
.content .center{
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.content .center .left{
  /* flex: 1; */
  width: 26%;
  text-align: left;
  padding-left: 42px;
  padding-top: 74px;
}
.content .center .left .left-img{
 width: 249px ;
 height: 489px;
 /* min-height: 0 !important;
 min-width: 0 !important; */
}
.content .center .middle{
  /* flex: 1; */
  width: 41%;
  padding-top: 109px;
  padding-left: 42px;
}
.content .center .middle h1{
  font-weight:bold;
  font-size: 38px;
  color:#333333;
  margin: 0;
}
.content .center .middle .contact{
  display: inline-block;
  width: 106px;
  height: 45px;
  background: #ffffff;
  padding-bottom: 159px;
}
.content .center .middle .contact a{
  color: #333333;
  font-size: 15px;
  text-align: center;
  display: inline-block;
  width: 100%;
  line-height: 46px;
  text-decoration: none;
  border: 1px solid #b2b2b2;
}
.content .center .middle h4{
   font-size: 18px;
   color:#333333;
   margin: 0;
   padding-top: 15px;
}
.content .center .middle .middle-text{
  padding-top: 34px;
  padding-bottom: 52px;
}
.content .center .middle .middle-text span{
 display: inline-block;
 font-size: 14px;
 color:#333333;
 padding-top: 10px;
}
.content .center .right{
  width: 35%;
  /* flex: 1; */
  text-align: right;
  padding-right: 42px;
  padding-top: 107px;
}
.content .center .right .right-img{
 width: 219px ;
 height: 487px ;
 /* min-height: 0 !important;
 min-width: 0 !important; */
}




