
.swiper-container-android .swiper-slide,
.swiper-wrapper {
  left: -184px;
}
.Imagedetails-swiper .swiper-button-prev {
  background-image: none;
  width: 26px;
  height: 80px;
  background-color: #e1e2e6;
  top: 22px;
  left: 0;
}
.Imagedetails-swiper .swiper-button-prev img {
  width: 26px;
  height: 26px;
  margin-top: 26px;
}
.Imagedetails-swiper .swiper-button-next {
  background-image: none;
  width: 26px;
  height: 80px;
  background-color: #e1e2e6;
  top: 22px;
  right: 0;
}
.Imagedetails-swiper .swiper-button-next img {
  width: 26px;
  height: 26px;
  margin-top: 26px;
}
.Imagedetails-swiper .swiper-button-prev:hover {
  background-color: #d4d5d9;
}
.Imagedetails-swiper .swiper-button-next:hover {
  background-color: #d4d5d9;
}
.label .el-row {
  padding-top: 16px;
  width: 500px;
}
.label .el-row button {
  display: inline-block;
  background-color: #f2f3f7;
  font-size: 13px;
  color: #4d4d4d;
  width: 68px;
  height: 30px;
  border-radius: 15px;
  padding: 9px 19px;
  margin-right: 9px;
  border: 1px solid #b3b3b3;
  margin-bottom: 9px;
}
.label .el-row button:hover {
  border: 1px solid #5bba9d;
  color: #5bba9d;
}
.el-button + .el-button {
  margin-left: 0px;
}



