
.el-card__header {
  padding: 0;
  border-bottom: 1px solid #cccccc;
}
.el-card__body {
  padding: 0 !important;
}
.coupleback-item .el-form-item__label {
  text-align: left;
  width: 100%;
  padding: 0 !important;
  font-size: 15px;
  color: #1a1a1a;
}
.coupleback-item .el-form-item__label::after {
  content: "*";
  color: #dc2e2e;
  font-size: 15px;
}
.option .el-form-item__label::after {
  content: "";
}
.coupleback-content .el-card__body {
  text-align: center;
  border: 1px solid #ffd821;
  margin: 44px 42px;
}
.coupleback-item .el-form-item__content .el-input__inner {
  background-color: #fff;
  color: #1a1a1a;
  font-size: 15px;
  height: 48px;
  border-radius: 0;
}
.coupleback-item .el-form-item__content .el-input__inner::-webkit-input-placeholder{
  color: #b3b3b3 !important;
  font-size:15px;
  /* WebKit browsers   */
}
.coupleback-item .el-form-item__content .el-input__inner:-moz-placeholder{
  color: #b3b3b3 !important;
  font-size:15px;
  /* Mozilla Firefox 4 to 18  */
}
.coupleback-item .el-form-item__content .el-input__inner::-moz-placeholder{
  color: #b3b3b3 !important;
  font-size:15px;
 /* Mozilla Firefox 19+  */
}
.coupleback-item .el-form-item__content .el-input__inner:-ms-input-placeholder{
  color: #b3b3b3 !important;
  font-size:15px;
  /* Internet Explorer 10+  ie*/
}
.coupleback-item .el-form-item__content .el-select {
  width: 100%;
}
.coupleback-item .el-input__suffix-inner {
  padding-right: 21px;
  font-size: 16px;
}
.coupleback-option .option .el-form-item__content {
  text-align: left;
}
.coupleback-option
  .option
  .el-form-item__content
  .el-radio-group
  .el-radio__inner {
  width: 16px;
  height: 16px;
}
.coupleback-option
  .option
  .el-form-item__content
  .el-radio-group
  .el-radio__label {
  font-size: 15px;
  color: #1a1a1a;
  padding-left: 6px;
}
.coupleback-option .option .el-form-item__content .el-radio{
  margin-right: 61px;
}
.coupleback-option .option .el-form-item__content .el-textarea {
  padding-top: 12px;
}
.coupleback-option .option .el-form-item__content .el-textarea__inner {
  width: 600px;
  height: 100px;
  border: 1px solid #b2b2b2;
  padding: 17px 0px 0px 13px;
}
.option-btn{
  padding-top: 45px;
}
.coupleback-button{
  background: #4bbf9e;
  border-radius: 0;
  width: 102px;
  height: 47px;
}
.coupleback-button span{
  color: #ffffff;
  font-size: 18px
}



