
.el-footer{
    width:100%;
    height: 200px !important;
    background: #5dbd93;
    padding:0px;
}
.footer{
   width:100%;
   height:200px;
}
.footer .left{
   height:200px;
  padding-left: 46px;
}
.footer .left .number{
   padding-top:43px;
}
.footer .left .introduce{
   padding-top:37px;
}
.footer .left span{
    display:block;
    font-size: 12px;
    color: #ffffff;
    line-height:16px;
}
.footer .right{
   width:100%;
   height:200px;
}
.footer .right a{
    text-decoration: none;
    font-size: 13px;
    color:#ffffff;
    padding-left: 62px;
    line-height: 100px
}
@media screen and (max-width: 768px) {
.el-footer{
        height: auto !important;
}
.footer .left{
        height:auto;
}
.footer .right{
         height:auto;
}
.footer .right a{
        padding-left: 28px;
}
}



